header {
	width: 100%;
	height: 72px;
	position: absolute;
	top: 0;
	left: 0;
	background: transparent;
	z-index: 3;
	.inner {
		width: 1180px;
		height: 100%;
		margin: 0 auto;

		.web_h1 {
			display: none;
		}
		.web_menu {
			width: 100%;
			padding-top: 28px;
			float: left;
			#gnb {
				width: 60%;
				height: 20px;
				float: left;
				> li {
					float: left;
					padding-right: 3vw;
					&:last-child {
						border-right: none;
					}
					> a {
						display: block;
						font: bold 16px/1 'arial';
						color: #eee;
						i {
							margin-right: 5px;
						}
					}
				}
			}
			.log {
				height: 100%;
				float: right;
				margin-right: 10px;
				li {
					float: left;
					height: 20px;
					display: flex;
					justify-content: center;
					align-items: center;
					margin-right: 10px;
					a {
						display: block;
						font: 16px/1 'arial';
						padding: 5px 10px;
						color: #eee;
						border: 1px solid #6d6d6d;
						border-radius: 3px;
						&:hover {
							background: #6d6d6d;
							color: #fff;
						}
					}
				}
			}
		}
		.btn_call {
			display: none;
			width: 20px;
			height: 20px;
			position: absolute;
			right: 30px;
			top: 30px;
			z-index: 5;
			&::before {
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				position: absolute;
				top: 0;
				left: 0;
				background: #000;
				transition: top 0.5s 0s, transform 0.5s 0.5s;
			}
			&::after {
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				position: absolute;
				bottom: 0;
				left: 0;
				background: #000;
				transition: bottom 0.5s 0s, transform 0.5s 0.5s;
			}
			span {
				display: block;
				width: 100%;
				height: 2px;
				position: absolute;
				top: 50%;
				left: 0;
				background: #000;
				margin-top: -1px;
				transition: 1s;
				text-indent: -9999px;
			}
			&.on {
				&::before {
					top: 50%;
					margin-top: -1px;
					transform: rotate(45deg);
				}
				&::after {
					bottom: 50%;
					margin-bottom: -1px;
					transform: rotate(-45deg);
				}
				span {
					transform: translateX(100%);
					opacity: 0;
				}
			}
		}
		.mob_menu {
			display: none;
			flex-direction: column;
			width: 50vw;
			height: 100vh;
			position: fixed;
			top: 0;
			left: -50vw;
			background: #1c1c1c;
			padding: 35px 30px;
			z-index: 6;
			transition: 0.5s;
			&.on {
				left: 0;
			}
			.mob_h1 {
				width: 143px;
				height: 45px;
				display: block;
				margin: 0 auto;
				margin-bottom: 20px;
				img {
					width: 100%;
					height: 100%;
				}
			}
			.mob_log {
				li {
					width: 70%;
					height: 40px;
					margin: 0 auto;
					text-align: center;
					a {
						display: block;
						font: 14px/1 'arial';
						padding: 5px 10px;
						color: #fff;
						border: 1px solid #6d6d6d;
						border-radius: 3px;
						text-align: center;
						&:hover {
							background: #6d6d6d;
							color: #fff;
						}
					}
				}
			}
			#mob_gnb {
				li {
					a {
						display: block;
						width: 100%;
						padding: 20px 0;
						border-bottom: 1px solid #444;
						font: bold 14px/1 'arial';
						color: #888;
						text-align: center;
					}
				}
			}
		}
	}
	&.on .inner .on_top {
		display: flex;
	}
}
